body{
    background-color: #26c6da;
}

h1{
  color: #FFC53D;
  font-family: 'Concert One';
  margin: 20px 0px;
  font-size: 40px;
  letter-spacing: 0.2rem;
}

.listText{
  margin-top: 50px;
}

.app{
    background-color :#0097a7;
    height: 95%;
    min-height: 600px;
    width: 70%;
    margin: 50px auto;
    position: relative;
    padding: 20px;
    border-radius: 30px;
}

.form input{
  background-color :#006064;
  border: 0;
  height: 50px;
  width: 40%;
  padding: 0 20px;
  margin: 10px 20%;
  border-radius: 10px;
  font-size: 16px;
  color: #ffffff;
  margin-right: 20px;
}

.form input:focus{
  outline: none;
}

.form button{
  text-align: center;
  position: relative;
  background: #FFC53D;
  border: none;
  width: 90px;
  height: 60px;
  border-radius: 20px;
  box-shadow:  0 2px 8px 8px rgba(10, 105, 135, .3);
  color: #006064;
  font-family: 'Concert One';
  font-size: 20px;

}

.headContainer{
  height: 40px;
}