h1{
    margin: 20px 10%;
    font-size: 30px;
    }

.container{
    display: flex;
    }

.icon{
    text-align: center;
    background: #FFC53D;
    border: none;
    width: 30px;
    padding-bottom: 3px;
    border-radius: 10px;
    box-shadow:  0 2px 8px 8px rgba(10, 105, 135, .3);
    color: #006064;
    font-family: 'Concert One';
    font-size: 20px;
    }

.list{
    color: #fff;
    width: 85%;
    margin: 20px auto;
    height: 50px;
    background-color: #006064;
    border-radius: 10px;
    }

.list p{
    padding: 10px;
    position: relative;
    }

.list p input{
    background-color: transparent;
    border: 0;
    width: 100%;
    color: #fff;
    font-size: 18px;
    font-family: 'Roboto';
    padding: 5px;
    }

.list p input:focus{
    outline: none;
    }
